



















import { Component, Prop, Vue } from 'vue-property-decorator';
import Axios, { AxiosError } from 'axios';

@Component
export default class ChatComponent extends Vue {
  @Prop() private messages!: string;

  @Prop() private gameId!: string;

  @Prop() private playerId!: string;

  @Prop() private playerName!: string;

  public text = '';

  // public appHost = 'http://localhost:8080';

  public appHost = 'https://joker.sgkperformance.com';

  send() {
    const message = `${this.playerName} > ${this.text}`;
    Axios.get(`${this.appHost}/sendMessage?playerId=${this.playerId}&gameId=${this.gameId}&message=${message}`).then((res) => {
      this.text = '';
      const container = this.$el.querySelector('.chat .body');
      if (container) {
        container.scrollTop = container.scrollHeight;
      }
    }).catch((error: AxiosError) => {
      if (error.response) {
        this.$alert(error.response.data.message);
      }
    });
  }
}
